import { extend, isFunction, isObject, ShapeFlags } from "@vue/shared";
import { componentPublicInstance } from "./componentPublicInstance";

// 
export const createComponentInstance = (vnode) => {
// instance(vnodepropsattrssetup)
const instance = {
  vnode,
  type: vnode.type, // 
  props: {}, // 
  attrs: {}, // 
  setupState: {}, // setup
  //ctx
  ctx: {}, // proxyinstance.props.xxxproxy.xxx
  proxy: {}, // ctx
  render: false, // 
  isMounted: false, // 
};
// instance.ctx  _ instance 
instance.ctx = { _: instance };// Vue  
return instance;
};

// 
export const setupComponet = (instance) => {
  // 
  instance.proxy = new Proxy(instance.ctx, componentPublicInstance as any);

  // instanceprops
  const { props, children } = instance.vnode;
  // 
  instance.props = props;
  instance.children = children; // slot
  // setuprender
  const shapeFlag = instance.vnode.shapeFlag & ShapeFlags.STATEFUL_COMPONENT;
  if (shapeFlag) {
    setupStateComponent(instance);
  } else {
    // 
  }
};


// 
function setupStateComponent(instance) {
    // setuprender
    // setup
    //   
    // 1setuppropscontext prop attrsemitrootslot
    // 2setup
    const Component = instance.type; // createVNodetyperootComponentsetuprender
    const { setup } = Component;
    //
    //const setupContext = createContext(instance); // 
    //setup(instance.props, setupContext); // setup

    //instance.proxy = new Proxy(instance.ctx, componentPublicInstance as any);

    if (setup) {
        const setupContext = createContext(instance); // 
        // setup  props 
        const setupResult = setup(instance.props, setupContext); // setup
        // setup12==>
        // instance.setupStaterender
        handlerSetupResult(instance, setupResult); 
      } else {
        // setupinstance.type.rendersetuprender
        finishComponentSetup(instance); // vnoderender
      }
      
}


// context prop attrsemitrootslot
function createContext(instance) {
  return {
    sttrs: instance.attrs,
    slots: instance.slots,
    emit: () => {},
    expose: () => {},
  };
}

// setup
function handlerSetupResult(instance, setupResult) {
    if (isFunction(setupResult)) {
      instance.render = setupResult; // setup==>render
    } else if (isObject(setupResult)) {
        //setupStatevueinstancesetup()
      instance.setupState = setupResult; // setup==>render
    }
  
    // renderrender
    finishComponentSetup(instance);
  }

// renderrender
function finishComponentSetup(instance) {
    // render
    const Component = instance.type; // createVNodetyperootComponentsetuprender
    if (!instance.render) {
      // renderinstancerenderhandlerSetupResultsetupsetupfalserender
      //template  HTML Vue @vue/compiler-dom JS 
      if (!Component.render && Component.template) {
        // TODO
      }
      instance.render = Component.render;
    }
    //console.log(instance.render);
  }
  
  

    