// h==>vnodecreateVNode==>
import { isObject,isArray } from "@vue/shared";
import { createVNode,isVnode } from "./vnode";

export function h(type, propsOrChildren, children) {
    // 
    const i = arguments.length;
    if (i === 2) {
      // 1+()
      if (isObject(propsOrChildren) && !isArray(propsOrChildren)) {
        //h("div", vnode)	vnode 	createVNode(type, null, [vnode])
        //h("div", [vnode])	vnode children	createVNode(type, null, propsOrChildren)
        if (isVnode(propsOrChildren)) {
          // h("div", vnode)
          // vnode
          return createVNode(type, null, [propsOrChildren]);
        }
        //=>props
        // h("div", { id: 'app' })
        return createVNode(type, propsOrChildren); // 
      } else {
        // 2+children
        // h("div", 'hello')  h("div", [vnode, vnode])
        return createVNode(type, null, propsOrChildren);
      }
    } else {
      if (i > 3) {
        children = Array.prototype.slice.call(arguments, 2); // children
      } else if (i === 3 && isVnode(children)) {//h("div", { id: "box" }, h("span"))vnode
        children = [children];//vnode
      }
      return createVNode(type, propsOrChildren, children);
    }
  }
  



