import { createVNode } from "./vnode";

// apiCreateAppdommount1vnode2rendervnode
export function apiCreateApp(render) {
  // createApp
  return function createApp(rootComponent, rootProps) {
    let app = {
      // 
      _components: rootComponent,
      _props: rootProps,
      _container: null,
      mount(container) {
        // 
        // console.log(renderOptionDom, rootComponent, rootProps, container);
        // 1dom vnode
        //vnode   DOM virtual DOM node
        // JavaScript  DOM  JS  DOM 
        let vnode = createVNode(rootComponent, rootProps);
        //console.log(vnode);
        // 2dom
        render(vnode, container);
        app._container = container;
      },
    };
    return app;
  };
}



