import { ShapeFlags } from "@vue/shared";
import { effect } from "@vue/reactivity";
import { apiCreateApp } from "./apiCreateApp";
import { createComponentInstance, setupComponet } from "./component";
import { CVnode, TEXT } from "./vnode";

// Vue3==>vnode==>render
export function createRender(renderOptionDom) {
  // dom
  const {
    insert: hostInsert,
    remove: hostRemove,
    patchProps: hostPatchProp,
    createElement: hostCreateElement,
    createText: hostCreateText,
    createComment: hostCreateComment,
    setText: hostSetText,
    setElementText: hostSetElementText,
  } = renderOptionDom;

  // effectrender()
  const setupRenderEffect = (instance, container) => {
    // effect()
    effect(function componentEffect() {
      // 
      if (!instance.isMounted) {
        // render
        const proxy = instance.proxy; // 
        // console.log("proxy");
        // console.log(proxy);
        const subTree = instance.render.call(proxy, proxy); // renderrenderrenderthisproxyrenderproxy
        // console.log("hvnode", subTree);
        patch(null, subTree, container); // vnodevnode
      }
    });
  };

  /** ------------------------------ */
  // 
  const processComponent = (n1, n2, container) => {
    if (n1 === null) {
      // 
      mountComponent(n2, container);
    } else {
      // 
    }
  };
  // domdom
  const mountComponent = (InitialVnode, container) => {
    // 1Vue3renderproxyproxysetupproxy.
    const instanece = (InitialVnode.component =
      createComponentInstance(InitialVnode)); // Vue3.0\packages\runtime-core\src\vnode.tsvnode
    // 2
    setupComponet(instanece);
    // 3effectrender
    setupRenderEffect(instanece, container);
  };

  /** ------------------------------ */
  const processElement = (n1, n2, container) => {
    if (n1 === null) {
      // 
      mountElement(n2, container);
    } else {
      // 
    }
  };
  // 
  const mountElement = (vnode, container) => {
    // ==>dom==container/
    const { shapeFlag, props, type, children } = vnode;
    // 1
    let el = hostCreateElement(type);
    // 2
    if (props) {
      for (const key in props) {
        hostPatchProp(el, key, null, props[key]);
      }
    }
    // 3children
    if (children) {
      if (shapeFlag & ShapeFlags.TEXT_CHILDREN) {
        console.log("", children);
        hostSetElementText(el, children); // h('div',{},'')childrenel
      } else if (shapeFlag & ShapeFlags.ARRAY_CHILDREN) {
        // 
        console.log("", children);
        mountChildren(el, children); // h('div',{},['',h('p',{},'')])childrenel
      }
    }
    // 4
    hostInsert(el, container);
  };
  // 
  const mountChildren = (container, children) => {
    for (let i = 0; i < children.length; i++) {
      // children[i]1['']2h('div',{},'')vnode
      // vnodepatch
      const child = CVnode(children[i]); // vnode
      // vnode
      patch(null, child, container);
    }
  };

  /** ------------------------------ */
  const processTxt = (n1, n2, container) => {
    if (n1 === null) {
      // ==>dom==>
      hostInsert(hostCreateText(n2.children), container);
    } else {
      // 
    }
  };

  /**---------------------------------------------------------- */

  // patchvnode
  const patch = (n1, n2, container) => {
    // vonodeshapeFlag/
    const { shapeFlag, type } = n2;
    switch (type) {
      case TEXT:
        // 
        processTxt(n1, n2, container);
        break;
      default:
        // shapeFlag && shapeFlag === ShapeFlags.ELEMENT
        if (shapeFlag & ShapeFlags.ELEMENT) {
          // (h)
          // console.log("");
          processElement(n1, n2, container);
        } else if (shapeFlag & ShapeFlags.STATEFUL_COMPONENT) {
          // 
          processComponent(n1, n2, container);
        }
    }
  };

  // vnode)
  const render = (vnode, container) => {
    // 
    patch(null, vnode, container);
  };

  // createAppcreateAppmountapp1vnode2rendervnode
  return {
    createApp: apiCreateApp(render),
  };
}

